export default {
	data() {
		return {
			code: '',
			access_token: '',
			appsercert: '',
			uid: '',
			src: '',
			name: '',
			suite_access_token: '',
			corpid: '',
			userid: '',
			mobile: '',
			email: '',
			corp_name:""
		}
	},
	methods: {
		submit() {
			
		}
	},
	onLoad(option) {
		// alert(option)
		// alert(JSON.stringify(option))
		// alert('code=')
		this.code = option.code
		// document.write = option.code
		// alert(this.code)
		uni.request({
			url: 'https://testapi.shiliucrm.com/v1/hwechat_callback/callbacktask/suiteaccesstoken',
			data: {
				code: this.code
			},
			success: (res) => {
				// alert(res)
				// alert(JSON.stringify(res))
				// alert(res.data.name)
				this.src = res.data.avatar
				this.name = res.data.name
				this.suite_access_token = res.data.suite_access_token
				this.corpid = res.data.corpid
				this.userid = res.data.userid
				this.mobile = res.data.mobile
				this.email = res.data.email
				this.corp_name = res.data.corp_name
				// alert(this.corpid)
				uni.request({
					url: 'https://testapi.shiliucrm.com/v2/user/user/qywx-login',
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						name: this.name,
						access_token: this.suite_access_token,
						cropid: this.corpid,
						userid: this.userid,
						avatar: this.src,
						mobile: this.mobile,
						email: this.email,
						corp_name:this.corp_name
					},
					success: (res) => {
						// alert(JSON.stringify(res))
						this.access_token = res.data.access_token
						this.appsercert = res.data.appsercert
						this.uid = res.data.uid
						if (this.access_token && this.appsercert && this.uid) {
							var obj = {
								access_token: this.access_token,
								appsercert: this.appsercert,
								uid: this.uid,
								username: this.name,
								src: this.src,
								cropid: this.corpid,
								userid: this.userid
							}
							uni.setStorageSync('login', obj)
							setTimeout(() => {
								uni.switchTab({
									url: '/pages/item/index'
								})
							}, 1000)
						} else {
							uni.showToast({
								title: '登录失败',
								icon: 'none'
							})
						}
					}
				})
			}
		})
	}
}
